import { Spinner, Subtitle, Text, Title, Wrapper } from "../styled";
import LoadingSpinner from "./spinner.svg";

export const Loading = () => {
    return (
        <Wrapper>
            <Spinner src={LoadingSpinner} alt="Ikonka adowania" />
            <Title>Trwa adowanie...</Title>
            <Subtitle>Prosimy o chwil cierpliwoci</Subtitle>
            <Text>
                Jeli adowanie trwa zbyt dugo, prosz odwiey stron.
            </Text>
        </Wrapper>
    )
};